import React, { Component } from 'react';
import './nprogress.css';
import './App.css';
import EventList from './EventList';
import CitySearch from './CitySearch';
import NumberOfEvents from './NumberOfEvents';
import { extractLocations, getEvents, checkToken, getAccessToken } from "./api";
import WelcomeScreen from './WelcomeScreen';
import { OfflineAlert } from './Alert';
import {ScatterChart, Scatter, XAxis, YAxis, CartesianGrid, Tooltip,  ResponsiveContainer} from 'recharts';
import { mockData } from './mock-data';
import EventGenre from './EventGenre';



class App extends Component {
  constructor(props) {
    super(props)

    this.state = {
      events: [],
      locations: [],
      numberOfEvents: 32,
      currentLocation: 'all',
      showWelcomeScreen: undefined,
       offlineText: ''

    }
  }

  // componentDidMount() {
  //   this.mounted = true;
  //   getEvents().then((events) => {
  //     if (this.mounted) {
  //       this.setState({
  //         events: events.slice(0, this.state.numberOfEvents),
  //         locations: extractLocations(events),
  //       });
  //     }
  //   });
  // }
  async componentDidMount() {
this.mounted = true;
const accessToken = localStorage.getItem('access_token');
const isTokenValid = (await checkToken(accessToken)).error ? false :
true;
const searchParams = new URLSearchParams(window.location.search);
const code = searchParams.get("code");
this.setState({ showWelcomeScreen: !(code || isTokenValid) });
if ((code || isTokenValid) && this.mounted) {
getEvents().then((events) => {
if (this.mounted) {
this.setState({ events, locations: extractLocations(events) });
}
});
}
if (!navigator.onLine) {
      this.setState({
        offlineText: 'You are currently offline, events may not be updated.'
      })
    } else {
      this.setState({
        offlineText: ''
      })
    }
}


  componentWillUnmount(){
    this.mounted = false;
  }




   updateEvents = (location, numberOfEvents) => {
    getEvents().then((events) => {
      const locationEvents =
        location === "all"
          ? events
          : events.filter((event) => event.location === location);

      const eventsToShow = locationEvents.slice(0, numberOfEvents);
      if (this.mounted) {
        this.setState({
          events: eventsToShow,
          currentLocation: location
        });
      }
    });
  };


  updateEventNumber = async (event) => {
    const eventCount = event.target.value ? parseInt(event.target.value) : 32;
    await this.setState({ numberOfEvents: eventCount });
    this.updateEvents(this.state.currentLocation, this.state.numberOfEvents);
  }

  // New Function
  getData = () => {
    const {locations, events} = this.state;
    const data = locations.map((location)=>{
      const number = events.filter((event) => event.location === location).length
      const city = location.split(', ').shift()
      return {city, number};
    })
    return data;
  };


  render() {

    if (this.state.showWelcomeScreen === undefined) return <div
    className="App" />
    return (
      <div className="App">
      <OfflineAlert text={this.state.offlineText} />
      <h1>Meet App</h1>
       <h4>Choose your nearest city</h4>
        <CitySearch locations={this.state.locations} updateEvents={this.updateEvents} />
        <NumberOfEvents eventCounter={this.state.numberOfEvents} updateEventNumber={(event) => this.updateEventNumber(event)} />
        <div className="data-vis-wrapper">
         <EventGenre events={this.state.events} />

        <ResponsiveContainer height={400} >
         <ScatterChart margin={{ top: 20, right: 20, bottom: 20, left: 20 }}>
           <CartesianGrid />
           <XAxis type="category" dataKey="city" name="city" />
           <YAxis
             allowDecimals={false}
             type="number"
             dataKey="number"
             name="number of events"
           />
           <Tooltip cursor={{ strokeDasharray: "3 3" }} />
           <Scatter data={this.getData()} fill="#8884d8" />
         </ScatterChart>
       </ResponsiveContainer>
       </div>
        <EventList events={this.state.events}/>
        <WelcomeScreen showWelcomeScreen={this.state.showWelcomeScreen}
          getAccessToken={() => { getAccessToken() }} />
      </div>

    );

  }
}
export default App;
